import { useState } from 'react';
import './index.css';
import axios from 'axios';

export default function AddCafe() {
    const headers = {
        "Content-type": "application/json;charset=UTF-8"
    }

    const [inputs, setInputs] = useState({
        cafeName: '',
        latitude: '',
        longitude: '',
        tag: '',
        cateName: '',
        university: '',
        price: '',
        openingHour: '',
        address: ''
      });
    
      const { cafeName, latitude, longitude, tag, cateName, university, price, openingHour, address } = inputs; //     
    
      const onChange = (e) => {
        const { value, name } = e.target; //  e.target  name  value  
        setInputs({
          ...inputs, //  input   
          [name]: value // name    value  
        });
      };

      const onSubmit = (e) => {
        e.preventDefault()
        // console.log (name, latitude, longitude, tag);
        
        let form = new FormData()
        console.log(inputs);
        form.append('cafeName', cafeName)
        form.append('latitude', latitude)
        form.append('longitude', longitude)

        axios.post("/api/v1/cafes", form, {headers})
            .then(function (response) {
                // response  
                console.log("");
                console.log(response);
            }).catch(function (error) {
                //  
                console.log(error);
                console.log("");
            })
      }

      const addCate = (e) => {
        e.preventDefault()
        axios.post("/api/v1/categorys",
            {
                name: cateName
            },
            {headers})
        .then(function(response) {
            console.log("  ")
            console.log(response)
        }).catch(function(error) {
            console.log(error);
            console.log("  ")
        })
      }
      
      axios.get("http://43.201.68.221:8080/api/v1/categorys")
        .then(function(response) {
            console.log(response.data)
        }) .catch(function(error) {
            console.log(error)
        })
    
      const tag_list = [' ', ' ', ' ', ' '];

    return (
        <div className='addCafe_container'>
            <form onSubmit={onSubmit}>
                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <input name="cafeName" onChange={onChange} value={cafeName} />
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <input name="latitude" onChange={onChange} value={latitude} />
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <input name="longitude" onChange={onChange} value={longitude} />
                </div>

                <div className='form_container2'>
                    <div className='label_container'>
                        <label htmlFor='form_label'>()</label>
                    </div>
                    
                    <div className='checkbox_container'>
                    {tag_list.map((tag, idx) => (
                        <div className='checkbox_container2' key={idx}>
                            <input type="checkbox" id={tag} /> 
                            <label htmlFor={tag}> {tag} </label>     
                        </div>
                    ))}
                    </div>
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <input name="cateName" onChange={onChange} value={cateName} 
                            style={{width: '200px'}}/>
                    <button className='addCategory_btn'onClick={addCate}></button>
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <select name="university" onChange={onChange} value={university}>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'></label>
                    </div>
                    <input name="price" onChange={onChange} value={price} />
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'> </label>
                    </div>
                    <input name="openingHour" onChange={onChange} value={openingHour} />
                </div>

                <div className='form_container'>
                    <div className='label_container'>
                        <label htmlFor='form_label'></label>
                    </div>
                    <input name="address" onChange={onChange} value={address} /> 
                </div>

                <div className='addButton'>
                    <button type="submit"></button>
                </div>
            </form>
        </div>
    );
}